import "../../Tables.module.css";
import React from "react";
import { useParams } from "react-router-dom";
import { useQuery } from "react-query3";
import { Script } from "types";
import { CodeEditor, FullEditor } from "components/ui/Editor/Editor";
import {
  LeftPanel,
  Page,
  PageContentBase,
  RightPanel,
  PageHeader,
} from "components/pages/Page";
import { Modal } from "components/ui/Modal";
import { capitalize } from "components/utils/utils";
const ResourceInfo = React.lazy(() => import("../ui/ResourceInfo"));
const ScriptViews = React.lazy(() => import("./components/ScriptViews"));
const RunScript = React.lazy(() => import("./components/RunScript"));

export default function ScriptPage() {
  const fullPath = useParams()["*"];
  const { data: script, refetch } = useQuery<Script>(`/script/${fullPath}`, {
    enabled: !!fullPath,
    refetchOnWindowFocus: false,
  });

  return (
    <Page resource={script}>
      <PageHeader>
        <ResourceInfo
          title={capitalize(script?.name)}
          subTitle={`#${script?.id}`}
          extra={[
            <Modal>
              {!script?.disableManualInvocation && (
                <RunScript script={script} children="Run" />
              )}
            </Modal>,
          ]}
        />
      </PageHeader>
      <PageContentBase>
        <LeftPanel>
          <CodeEditor refetch={() => refetch()}>
            <FullEditor title="Code Editor" />
          </CodeEditor>
        </LeftPanel>
        <RightPanel>
          <ScriptViews />
        </RightPanel>
      </PageContentBase>
    </Page>
  );
}
