import React, { Fragment } from "react";
import { Button, Card, message, Spin, Tooltip } from "antd";
import { callAll } from "components/utils/utils";
import queryClient, { useMutation } from "components/utils/queryClient";
import {
  CopyFilled,
  SaveFilled,
  ZoomInOutlined,
  ZoomOutOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import RoleGuard from "components/standalone/role-guard";
import Editor, {
  useMonaco,
  EditorProps,
  loader,
  Monaco,
} from "@monaco-editor/react";
import { useThemeSwitcher } from "react-css-theme-switcher";
import { psuDark, psuLight } from "../../editor/themes/EditorThemes";
import useAppStore from "components/context/app/Hooks";
import { AccessControlTypes, Accessible } from "types";
import { editor, MarkerSeverity } from "monaco-editor";
import { LeftPanelSizeButton, usePageContext } from "components/pages/Page";
import useClipboard from "use-clipboard-hook";
import { CardProps } from "antd/es";
import axios from "axios";

loader.config({ paths: { vs: "/admin/vs" } });
loader.init().then(monaco => {

  monaco.languages.registerDocumentFormattingEditProvider("powershell", {
    displayName: "PowerShell",
    provideDocumentFormattingEdits: async (model, options, token) => {
      let { data } = await axios.post(`/api/v1/editor/format`, model.getValue(), {
        headers: {
          'content-type': 'text/plain'
        }
      });

      return [{
        text: data,
        range: {
          startColumn: 1,
          startLineNumber: 1,
          endLineNumber: model.getLineCount(),
          endColumn: model.getLineMaxColumn(model.getLineCount())
        }
      }];
    }
  })

  const triggerChars = ["$", "-", "\\", "/", ":", "."]

  monaco.languages.registerCompletionItemProvider("powershell", {
    triggerCharacters: triggerChars,
    provideCompletionItems: async (model, position, context) => {
      if (triggerChars.findIndex(x => x === context.triggerCharacter) !== -1) {
        let { data } = await axios.post(`/api/v1/editor/complete`, {
          content: model.getValue(),
          cursorPosition: model.getOffsetAt(position)
        });

        const suggestions = data?.map(x => {
          return {
            insertText: x.text,
            label: x.text,
            documentation: x.help,
            kind: monaco.languages.CompletionItemKind.Property,
          }
        });

        return {
          incomplete: false,
          suggestions
        }
      }

      return null;
    }
  });
})

interface EditorContextProps {
  isReadOnly: boolean;
  setIsReadOnly: React.Dispatch<React.SetStateAction<boolean>>;
  onSave: (key: any, action: any, resource: any) => Promise<void>;
  monaco: Monaco;
  editor: editor.IStandaloneCodeEditor;
  userData: Accessible;
  handleEditorDidMount: (editor: any, monaco: any, resource: any) => void;
  currentTheme: string;
  onCopy: () => void;
  zoomIn: () => void;
  zoomOut: () => void;
  isSaving: boolean;
}

const EditorContext = React.createContext<EditorContextProps>(null);

function CodeEditor({ children, refetch = () => { } }) {
  const [isReadOnly, setIsReadOnly] = React.useState(false);
  const [isSaving, setIsSaving] = React.useState(false);
  const { mutateAsync } = useMutation<any>();
  const { currentTheme } = useThemeSwitcher();
  const { userData } = useAppStore();
  const monaco = useMonaco();

  const { copy } = useClipboard({
    onSuccess: (text) => message.success(`Copied.`),
  });

  const editorRef = React.useRef<editor.IStandaloneCodeEditor>(null);


  const onSave = React.useCallback(
    async (key: string, action: any, resource: any) => {

      setIsSaving(true);
      setIsReadOnly(true);

      if (key === "/script") {
        resource["content"] = editorRef?.current?.getValue();
      }

      if (key === "/endpoint") {
        key = resource?.resourceInfo?.self;
        resource["scriptBlock"] = editorRef?.current?.getValue();
      }

      if (key === "/dashboard") {
        key = resource?.resourceInfo?.self;
        resource["content"] = editorRef?.current?.getValue();
      }

      if (key.startsWith("/role")) {
        key = resource?.resourceInfo?.self;
        resource["policy"] = editorRef?.current?.getValue();
      }

      if (key === "/settings") {
        key = "/settings/authentication/1";
        resource["settings"] = editorRef?.current?.getValue();
      }

      if (key.startsWith('/module')) {
        key = resource?.resourceInfo?.self;
        resource["content"] = editorRef?.current?.getValue();
      }

      if (resource?.resourceInfo?.schemaName === "configuration") {
        resource = editorRef?.current?.getValue({
          preserveBOM: true,
          lineEnding: "\r\n",
        });
      }

      await mutateAsync(
        { key, action, resource },
        {
          onError: (error) => {
            setIsSaving(false);
            setIsReadOnly(false);
            message.error(error.message);
          },
          onSuccess: (data, variables) => {
            setIsSaving(false);
            setIsReadOnly(false);
            message.success("Changes saved!");
            queryClient.refetchQueries(key);
            refetch()
          },
        }
      );
    },
    [mutateAsync, refetch]
  );


  const handleEditorDidMount = React.useCallback(
    (editor, monaco, resource) => {
      editorRef.current = editor;

      monaco.languages.register({ id: "psulog" });
      monaco.languages.setMonarchTokensProvider("psulog", {
        tokenizer: {
          root: [
            [/\[error.*/, "custom-error"],
            [/\[verbose.*/, "custom-verbose"],
            [/\[warning.*/, "custom-warning"],
            [/\[debug.*/, "custom-debug"],
            [/\[information.*/, "custom-info"],
          ],
        },
      });

      const debounce = (callback, delay) => {
        let timer;
        return (...args) => {
          clearTimeout(timer);
          timer = setTimeout(() => callback(...args), delay);
        }
      }

      const model = editor.getModel();
      model.onDidChangeContent(() => {
        var versionId = model.getVersionId();
        debounce(async () => {
          var newVersionId = model.getVersionId();
          if (versionId !== newVersionId) return;

          let { data } = await axios.post(`/api/v1/editor/parse`, model.getValue(), {
            headers: {
              'content-type': 'text/plain'
            }
          });

          const markerData = data.map(x => {
            return {
              startColumn: x.token.startColumn,
              endColumn: x.token.endColumn,
              startLineNumber: x.token.startLine,
              endLineNumber: x.token.endLine,
              severity: MarkerSeverity.Error,
              message: x.message
            }
          });

          monaco.editor.setModelMarkers(model, 'powershell', markerData);
        }, 300)();

      });

      if (resource) {
        editor.addCommand(monaco.KeyMod.CtrlCmd | monaco.KeyCode.KEY_S, () => onSave(resource?.resourceInfo?.parent, "update", resource));
      }

      editor.addAction({
        // An unique identifier of the contributed action.
        id: "refresh",

        // A label of the action that will be presented to the user.
        label: "Refresh",

        // An optional array of keybindings for the action.
        keybindings: [monaco.KeyCode.F5],

        // A precondition for this action.
        precondition: null,

        // A rule to evaluate on top of the precondition in order to dispatch the keybindings.
        keybindingContext: null,

        contextMenuGroupId: "navigation",

        contextMenuOrder: 1.5,
        run: function (ed) {
          refetch();
          return null;
        },
      });
      editor.addAction({
        // An unique identifier of the contributed action.
        id: "format",

        // A label of the action that will be presented to the user.
        label: "Format",

        // An optional array of keybindings for the action.
        keybindings: [monaco.KeyCode.F8],

        // A precondition for this action.
        precondition: null,

        // A rule to evaluate on top of the precondition in order to dispatch the keybindings.
        keybindingContext: null,

        contextMenuGroupId: "navigation",

        contextMenuOrder: 1.5,

        run: () => {
          editor.trigger(
            "anyString",
            "editor.action.formatDocument",
            null
          );
          // editorRef.current.getAction('editor.action.formatDocument').run();
          return null;
        },
      });
    },
    [refetch, onSave]
  );

  React.useEffect(() => {
    if (monaco) {
      monaco.editor.defineTheme("psu-dark", psuDark);
      monaco.editor.defineTheme("psu-light", psuLight);
      monaco.editor.setTheme(
        currentTheme === "dark" ? "psu-dark" : "psu-light"
      );
    }
  }, [currentTheme, monaco]);

  const onCopy = React.useCallback(() => copy(editorRef?.current?.getValue()), [
    copy,
  ]);
  const zoomIn = () =>
    editorRef?.current?.trigger("keyboard", "editor.action.fontZoomIn", {});
  const zoomOut = () =>
    editorRef?.current?.trigger("keyboard", "editor.action.fontZoomOut", {});

  const value = React.useMemo(
    () => ({
      isReadOnly,
      setIsReadOnly,
      onSave,
      monaco,
      editor: editorRef?.current,
      userData,
      handleEditorDidMount,
      currentTheme,
      onCopy,
      zoomIn,
      zoomOut,
      isSaving
    }),
    [
      currentTheme,
      handleEditorDidMount,
      isReadOnly,
      monaco,
      onCopy,
      onSave,
      userData,
      isSaving
    ]
  );

  return (
    <EditorContext.Provider value={value}>{children}</EditorContext.Provider>
  );
}

function SaveButton({ children: child, resource }) {
  return (
    <RoleGuard requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Edit} accessControls={resource?.accessControls} allowedWithOneWayGitSync={false}>
      <Tooltip title="Save">
        {React.cloneElement(child, {
          type: child?.props?.type || "text",
          icon: child?.props?.icon || <SaveFilled />,
          onClick: callAll(child?.props?.onClick),
        })}
      </Tooltip>
    </RoleGuard>
  );
}

function BaseEditor({ ...props }: EditorProps) {
  const { currentTheme, handleEditorDidMount } = React.useContext(
    EditorContext
  );
  return (
    <Editor
      theme={currentTheme === "dark" ? "psu-dark" : "psu-light"}
      language="powershell"
      loading={<Spin tip="Loading content..." />}
      height="calc((100vh - 256px) - 48px)"
      onMount={(editor, monaco) => handleEditorDidMount(editor, monaco, null)}
      {...props}
    />
  );
}

type FullEditorProps = {
  title?: string;
  showSave?: boolean;
  refetch?: () => void;
  options?: EditorProps["options"];
  actions?: CardProps["extra"]
  hideToolbar?: boolean;
} & EditorProps;

function FullEditor({
  title,
  options,
  showSave = true,
  actions,
  hideToolbar,
  ...props
}: FullEditorProps) {
  const {
    handleEditorDidMount,
    currentTheme,
    onSave,
    onCopy,
    zoomIn,
    zoomOut,
    isSaving
  } = React.useContext(EditorContext);
  const { resource } = usePageContext();

  return (
    <Card
      bordered={false}
      bodyStyle={{ padding: 0 }}
      title={title}
      extra={
        !hideToolbar && <Fragment>
          {actions}
          <Tooltip title="Zoom in">
            <Button
              type="text"
              icon={<ZoomInOutlined />}
              onClick={() => zoomIn()}
            />
          </Tooltip>
          <Tooltip title="Zoom out">
            <Button
              type="text"
              icon={<ZoomOutOutlined />}
              onClick={() => zoomOut()}
            />
          </Tooltip>
          {showSave && (
            <SaveButton resource={resource}>
              <Button
                icon={isSaving ? <LoadingOutlined /> : null}
                onClick={callAll(() =>
                  onSave(resource?.resourceInfo?.parent, "update", resource)
                )}
              />
            </SaveButton>
          )}
          <Tooltip title="Copy">
            <Button
              type="text"
              icon={<CopyFilled />}
              onClick={() => onCopy()}
            />
          </Tooltip>
          <LeftPanelSizeButton>
            <Button />
          </LeftPanelSizeButton>
        </Fragment >
      }
    >
      <BaseEditor
        language="powershell"
        //@ts-ignore
        height="calc((100vh - 256px) - 48px)"
        value={resource?.content || resource?.scriptBlock || resource?.settings || props?.value}
        onMount={(editor, monaco) => handleEditorDidMount(editor, monaco, resource)}
        theme={currentTheme === "dark" ? "psu-dark" : "psu-light"}
        loading={props?.loading || <Spin tip="Loading content..." />}
        {...props}
        options={{
          wordWrap: "on",
          wrappingIndent: "same",
          minimap: {
            enabled: false,
          },
          lineHeight: 20,
          lineNumbersMinChars: 5,
          scrollBeyondLastLine: false,
          hideCursorInOverviewRuler: true,
          overviewRulerLanes: 0,
          overviewRulerBorder: false,
          ...options,
        }}
      />
    </Card >
  );
}

const useEditorContext = () => React.useContext(EditorContext);

export { CodeEditor, BaseEditor, FullEditor, SaveButton, useEditorContext };
